.form {
  position: relative;
  display: flex;
  align-items: center;
  justify-content: flex-end;

  --spacing: 3rem;
  --button-width: 18rem;
  --field-width: calc(100% - var(--spacing) - var(--button-width));

  &--result {
    --button-width: 28rem;
  }

  @media (min-width: $stop-scaling) {
    --spacing: 30px;
    --button-width: 180px;

    &--result {
      --button-width: 280px;
    }
  }

  @media (orientation: portrait) {
    --spacing: 1rem;
    --button-width: 4rem;
  }

  @media (max-width: $tablet) and (orientation: landscape) {
    --spacing: 3rem;
    --button-width: 6rem;
  }

  @media (max-width: $mobile) and (orientation: landscape) {
    --spacing: 1rem;
    --button-width: 4rem;
  }
}

.form__label {
  position: absolute;
  bottom: 100%;
  left: 50%;
  transform: translateX(-50%);
  margin-bottom: 1.2rem;
  width: 100%;
  text-transform: uppercase;
  text-align: center;
  font-size: 1.6rem;
  line-height: 1.2;
  opacity: 0;
  pointer-events: none;

  @media (min-width: $stop-scaling) {
    margin-bottom: 12px;
    font-size: 16px;
  }

  @media (orientation: portrait) {
    opacity: 1;
    pointer-events: auto;
  }
}

.form__field {
  flex: none;
  margin-right: var(--spacing);
  padding: 0 5.6rem;
  height: 6rem;
  text-transform: uppercase;
  font: inherit;
  font-size: 2.4rem;
  line-height: 1.2;
  color: $c-dark;
  border: 0;
  border-radius: 3rem;
  outline: none;

  width: 0;
  transition-property: width, padding;
  transition-timing-function: ease-out;
  transition-duration: 450ms;

  @include for-active-screen {
    width: var(--field-width);
  }
  @include for-not-active-screen {
    padding: 0;
  }

  &:focus {
    border: 1px solid $c-purple;
  }

  @media (min-width: $stop-scaling) {
    padding: 0 56px;
    height: 60px;
    font-size: 24px;
    border-radius: 30px;
  }

  @media (orientation: portrait) {
    padding: 0 2.5rem;
    height: 4rem;
    font-size: 1.6rem;
  }

  @media (max-width: $mobile) and (orientation: landscape) {
    padding: 0 2.5rem;
    height: 4rem;
    font-size: 1.6rem;
  }
}

.form__button {

  width: var(--button-width);

  @media (orientation: portrait) {
    position: relative;
    flex: none;
    padding: 0;
    height: var(--button-width);
    font-size: 0;
  }

  @media (max-width: $tablet) and (orientation: landscape) {
    position: relative;
    flex: none;
    padding: 0;
    height: var(--button-width);
    font-size: 0;
  }

  @media (max-width: $mobile) and (orientation: landscape) {
    height: var(--button-width);
  }

  svg {
    @include center;
    display: none;
    width: 1.6rem;
    height: 1.8rem;

    @media (min-width: $stop-scaling) {
      width: 16px;
      height: 18px;
    }

    @media (orientation: portrait) {
      display: block;
    }

    @media (max-width: $tablet) and (orientation: landscape) {
      display: block;
    }
  }
}

